@import url(https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100;0,200;0,400;0,600;1,100;1,200;1,400;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
.corner-element {
  position: absolute;
  border-style: solid;
  border-width: 1px;
  border-color: #e4e4e4;
  width: 150px;
  height: 25px;
  opacity: .6;
}

.corner-text {
  margin: 4px;
  font-size: 14px;
  line-height: 14px;
  font-weight: 200;
  text-transform: uppercase;
}

.corner-element.ul {
  top: 14px;
  left: 14px;
  border-right: 0;
  border-bottom: 0;
}

.corner-element.ur {
  top: 14px;
  right: 14px;
  text-align: right;
  border-bottom: 0;
  border-left: 0;
}

.corner-element.bl {
  bottom: 14px;
  left: 14px;
  border-top: 0;
  border-right: 0;
}

.corner-element.br {
  bottom: 14px;
  right: 14px;
  text-align: right;
  border-top: 0;
  border-left: 0;
}

body {
  margin: 0;
  font-family: 'Exo 2', sans-serif !important;
}

.Headline-container {
  height: 100vh;
  width: 100vw;
}

.App-headline {
  font-size: 60px;
  font-weight: 600;
  display: inline-block;
  line-height: 1.0;
  font-style:italic;
}

.App-subline {
  font-weight: 200;
  margin-top: -8px;
  margin-bottom: 6px;
  font-size: 30px;
  opacity: .5;
  display: inline-block;
  line-height: 1;
  font-style: italic;
}

.subtitle {
  display: block;
  font-size: 18px;
  font-weight: 400;
  font-style: italic;
}

.social-link-container {
  max-height: 400px;
  padding: 0;
  margin: 0;
  display: block;
  overflow-y: hidden;
}

.social-link-container.closed {
  max-height: 0px;
  overflow: visible;
}

.social-link-container.open {
  max-height: 0px;
  -webkit-animation-name: expand-in;
          animation-name: expand-in;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
 
.social-line {
  position: relative;
  display:block;
  text-transform: uppercase;
  font-style: italic;
}

.social-line.inline-social {
  display: inline-block;
}

.vl {
  border-left: 1.5px solid #e4e4e4;
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-left: -6px;
  -webkit-animation-name: divider-entry;
          animation-name: divider-entry;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.social-span {
  display: inline-block;
  padding-left: 5px;
}

@-webkit-keyframes divider-entry {
  0% {
    opacity: 0;
    height: 0px;
    padding-left: 0px;
    padding-right: 0px;
  }
  50% {
    -webkit-transform: rotate(0deg) translate(0px, 0px);
            transform: rotate(0deg) translate(0px, 0px);
  }
  100% {
    opacity: 1;
    height: 14px;
    padding-left: 5px;
    padding-right: 5px;
    -webkit-transform: rotate(20deg) translate(10px, -1.5px);
            transform: rotate(20deg) translate(10px, -1.5px);
  }
}

@keyframes divider-entry {
  0% {
    opacity: 0;
    height: 0px;
    padding-left: 0px;
    padding-right: 0px;
  }
  50% {
    -webkit-transform: rotate(0deg) translate(0px, 0px);
            transform: rotate(0deg) translate(0px, 0px);
  }
  100% {
    opacity: 1;
    height: 14px;
    padding-left: 5px;
    padding-right: 5px;
    -webkit-transform: rotate(20deg) translate(10px, -1.5px);
            transform: rotate(20deg) translate(10px, -1.5px);
  }
}

.low-opacity {
  opacity: 0;
}

@media only screen and (min-width: 900px) { 
  .low-opacity {
    opacity: .4;
  }
}



.entry-anim {
  opacity: 0;
  -webkit-animation-name: blink-in;
          animation-name: blink-in;
  -webkit-animation-duration: .75s;
          animation-duration: .75s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.entry-anim-harsh {
  opacity: 0;
  -webkit-animation-name: blink-in;
          animation-name: blink-in;
  -webkit-animation-duration: .5;
          animation-duration: .5;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.expand-anim {
  opacity: 0;
  -webkit-animation-name: expand;
          animation-name: expand;
  -webkit-animation-duration: .75s;
          animation-duration: .75s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.welcome-text {
  display: inline-block;
  -webkit-animation-name: shrink-out;
          animation-name: shrink-out;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  overflow: hidden;
  vertical-align: top;
  white-space: nowrap;
}

.title-block {
  display: inline-block;
  padding-right: 10px;
}

.App-body {
  background-color: #2a2630 !important;
  color: #FFFFFF;
}

.App-welcome {
  min-height: 100vh;
  min-width: 100vw;
}

.App-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.App-link {
  color: #61dafb;
}

a {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 18px;
  line-height: 1;
  text-decoration: none;
}

a:hover {
  color: #c7a5cf;
  -webkit-animation-name: link-hover;
          animation-name: link-hover;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  text-decoration: none;
}

hr {
  border-top: 1px #e4e4e4 solid;
  margin-bottom: 14px;
  margin-left: 0;
}

