@-webkit-keyframes slide-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes expand {
  0% {
    max-width: 0%;
    opacity: 0;
  }
  100% {
    max-width: 100%;
    opacity: 1;
  }
}

@keyframes expand {
  0% {
    max-width: 0%;
    opacity: 0;
  }
  100% {
    max-width: 100%;
    opacity: 1;
  }
}

@-webkit-keyframes shrink-out {
  0% {
    max-width: 100%;
    opacity: 100%;
  }
  50% {
    opacity: 100%;
    padding-right: 10px;
  }
  100% {
    max-width: 0%;
    opacity: 0%;
    padding-right: 0px;
  }
}

@keyframes shrink-out {
  0% {
    max-width: 100%;
    opacity: 100%;
  }
  50% {
    opacity: 100%;
    padding-right: 10px;
  }
  100% {
    max-width: 0%;
    opacity: 0%;
    padding-right: 0px;
  }
}

